$sl-lightbox-transition: opacity 0.5s ease !default;
$sl-arrow-side-position: 15px !default;
$sl-caption-margin: 10px 0 0 !default;

.nx-slick-lightbox {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: black;
  -webkit-transition: $sl-lightbox-transition;
  transition: $sl-lightbox-transition;
  .nx-slick-loading .nx-slick-list {
    background-color: transparent; }
  .nx-slick-prev {
    left: $sl-arrow-side-position; }
  .nx-slick-next {
    right: $sl-arrow-side-position; } }

.nx-slick-lightbox-hide {
  opacity: 0;
  &.nx-slick-lightbox-ie {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0); } }

.nx-slick-lightbox-hide-init {
  position: absolute;
  top: -9999px;
  opacity: 0;
  &.nx-slick-lightbox-ie {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0); } }

.nx-slick-lightbox-inner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.nx-slick-lightbox-slick-item {
  text-align: center;
  overflow: hidden;
  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.25em; }

  .nx-slick-caption-bottom & {
    .nx-slick-lightbox-slick-item {
      .nx-slick-lightbox-slick-caption {
        position: absolute;
        bottom: 0;
        left: 0;
        text-align: center;
        width: 100%;
        margin-bottom: 20px; } } }

  .nx-slick-caption-dynamic & {
    .nx-slick-lightbox-slick-item {
      .nx-slick-lightbox-slick-caption {
        display: block;
        text-align: center; } } } }

.nx-slick-lightbox-slick-item-inner {
  display: inline-block;
  vertical-align: middle;
  max-width: 90%;
  max-height: 90%; }

.nx-slick-lightbox-slick-img {
  margin: 0 auto;
  display: block;
  max-width: 90%;
  max-height: 90%; }

.nx-slick-lightbox-slick-caption {
  display: inline-block;
  margin: $sl-caption-margin;
  color: white;
  bottom: 0;
  left: 0;
  text-align: center;
  width: 100%;
  margin-bottom: 20px; }

// used slick's button css
.nx-slick-lightbox-close {
  position: absolute;
  top: 15px;
  right: 15px;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  padding: 0;
  border: none;
  &:focus {
    outline: none; }
  &:before {
    font-family: "slick";
    font-size: 20px;
    line-height: 1;
    color: white;
    opacity: 0.85;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: 'Ã—'; } }
